import React from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from 'react-router-dom'
import './App.css'
import Home from './components/frontend/Home'
import Login from './components/frontend/auth/Login'
import Register from './components/frontend/auth/Register'
import ForgotPassword from './components/frontend/auth/ForgotPassword'
import PasswordReset from './components/frontend/auth/PasswordReset'
import Dashboard from './components/admin/Dashboard' // Example admin component
import axios from 'axios'
import AdminPrivateRoute from './AdminPrivateRoute'

axios.defaults.baseURL = 'http://localhost:8000/'
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.post['Accept'] = 'application/json'
axios.defaults.withCredentials = true
axios.defaults.withXSRFToken = true
axios.interceptors.request.use(function (config) {
  const token = localStorage.getItem('auth_token')
  config.headers.Authorization = token ? `Bearer ${token}` : ''
  return config
})

function App() {
  const isAuthenticated = !!localStorage.getItem('auth_token')

  return (
    <div className='App'>
      <Router>
        <Routes>
          <Route path='/' element={<Home />} />
          <Route
            path='/login'
            element={isAuthenticated ? <Navigate to='/' /> : <Login />}
          />
          <Route
            path='/register'
            element={isAuthenticated ? <Navigate to='/' /> : <Register />}
          />

          <Route
            path='/forgot-password'
            element={isAuthenticated ? <Navigate to='/' /> : <ForgotPassword />}
          />

          <Route path='/password/reset/:token' element={<PasswordReset />} />
          {/* <Route
            path='/reset-password/:token'
            element={isAuthenticated ? <Navigate to='/' /> : <PasswordReset />}
          /> */}

          <Route
            path='/admin/*'
            element={
              <AdminPrivateRoute>
                <Routes>
                  <Route path='dashboard' element={<Dashboard />} />
                  {/* Add other admin routes here */}
                </Routes>
              </AdminPrivateRoute>
            }
          />
        </Routes>
      </Router>
    </div>
  )
}

export default App
